body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,*:before,*:after {
  box-sizing: border-box;
}

.App{
  max-width: calc(9px * 35 + 1px * 2); /* a. (マスの幅) * 個数 + (ボーダーの幅) * 本数 */
  text-align: center;
}

.is-hidden{
  /* visibility: hidden !important; */
  display: none;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url('img/BG.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.dots {
  height: 9px;
  width: 9px;
  background: #0f1d1d;
}

.field {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2px;
  color: white;
  background: #0f1d1d;
  border-radius: 0 0 4px 4px;
  -webkit-border-radius: 0 0 4px 4px;
  -moz-border-radius: 0 0 4px 4px;
  -ms-border-radius: 0 0 4px 4px;
  -o-border-radius: 0 0 4px 4px;
  border: 1px solid #ffffff30;
  box-shadow: 0px 4px 1px 1px #0C1818, 0px 6px 1px 1px #060F0D;
}

.snake{
  background: white;
}

.food{
  background: pink;
}

.navigation{
  display: flex;
  margin-top: 4px;
  justify-content: space-evenly;
  color:white;
  font-weight: bold;
  background: #020705;
  box-shadow: 0px 4px 1px 1px #020604, 0px 6px 1px 1px #010403;
  border: 1px solid #ffffff30;
}

.navigation-item{
  padding: 16px;
  display: flex;
  align-items: center;
}

.navigation-item-number-container {
  display: flex;
  padding: 0 8px;
  align-items: center;
  margin-left: 8px;
}

.num-board {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #0f1d1d;
  height: 48px;
  width: 24px;
}

.difficulty-number{
  margin-right: 8px;
}

.difficulty-button{
  cursor: pointer;
  width: 8px;
  height: 8px;
  border: 8px solid #0f1d1d;
  margin: 8px;
}

.difficulty-button-container{
  background: #0f1d1d;
}

.difficulty-up{
  margin-top: 4px;
  border-bottom: 8px solid #f0f0f050;
  transition: all ease .5s;
  -webkit-transition: all ease .5s;
  -moz-transition: all ease .5s;
  -ms-transition: all ease .5s;
  -o-transition: all ease .5s;
}
.difficulty-up:hover{
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
  border-bottom: 8px solid white;
}

.difficulty-up:active{
  transform: scale(.8);
  -webkit-transform: scale(.8);
  -moz-transform: scale(.8);
  -ms-transform: scale(.8);
  -o-transform: scale(.8);
  opacity: .8;
}

.difficulty-down{
  margin-bottom: 4px;
  border-top: 8px solid #f0f0f050;
  transition: all ease .5s;
  -webkit-transition: all ease .5s;
  -moz-transition: all ease .5s;
  -ms-transition: all ease .5s;
  -o-transition: all ease .5s;
}

.difficulty-down:hover{
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
  border-top: 8px solid white;
}
.difficulty-down:active{
  transform: scale(.8);
  -webkit-transform: scale(.8);
  -moz-transform: scale(.8);
  -ms-transform: scale(.8);
  -o-transform: scale(.8);
  opacity: .8;
}


.title-container{
  background: #fff;
  border-radius: 4px 4px 0px 0px;
  -webkit-border-radius: 4px 4px 0px 0px;
  -moz-border-radius: 4px 4px 0px 0px;
  -ms-border-radius: 4px 4px 0px 0px;
  -o-border-radius: 4px 4px 0px 0px;
  box-shadow: 0px 4px 1px 1px #97A5A5,0px 6px 1px 1px #3C4A4A,0px 0px 1px 1px #3C4A4A;
  position: relative;
}

.title{
  margin: 0;
  padding: 8px;
  font-size: 1.1rem;
}

.btn{
  border: 0;
  cursor: pointer;
  color: white;
  border-radius: 4px;
  width: 100%;
  font-weight: bold;
  padding: 8px 16px;
  font-size: 1rem;
  text-transform: uppercase;
  transition: all ease .2s;
}

.btn-init,.btn-suspended{
  box-shadow: 0px 4px 1px 1px #199A4B, 0px 6px 1px 1px #0B5420;
  background: #23CE6B;
}

.btn-playing{
  box-shadow: 0px 4px 1px 1px #D4A752, 0px 6px 1px 1px #956933;
  background: #FFD166;
}

.btn-gameover{
  box-shadow: 0px 4px 1px 1px #CE3959, 0px 6px 1px 1px #8B1C2C;
  background: #EF476F;
}

.btn:focus{
  outline: 0;
}

.btn:active {
  box-shadow: 0px 0px 0px 0px;
  transform: translateY(6px);
}

.btn-init:active,
.btn-suspended:active {
  background: #20BD60;
}

.btn-playing:active {
  background: #EABC5C;
}

.btn-gameover:active {
  background: #DE4064;
}


/* 操作ボタン */
.manipulation-panel {
  margin-top: 2px;
  display: flex;
  justify-content: center;
}

.manipulation-btn {
  min-width: 106px;
  margin: 4px 2px;
  background: #52B4E8;
  box-shadow: 0px 4px 1px 1px #4293C4, 0px 6px 1px 1px #21507B;
}

.btn-left,
.btn-right
{
  min-width: 106px;
  width: 106px;
}
.btn-left {
  margin-left: 0;
}
.btn-right {
  margin-right: 0;
}

.btn-up,
.btn-down
{
  margin-left: 0px;
  margin-right: 0px;
}

.sound-btn{
  min-width: 30px;
  width: 30px;
  height: 30px;
  padding: 0;
  position: absolute;
  right: 5px;
  top: 5px;
}

.music-btn{
  color: white;
  background: #61dafb;
}

.music-btn-mute{
  color: rgb(51, 51, 51);
  background: #757575;
}

.popup_image{
  /* background: olivedrab; */
  width: 768px;
  height: 600px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  z-index: 100;
  box-shadow: 0px 0px 50px 20px #8f8f8f;
}

.popup_image2{
  /* background: olivedrab; */
  width: 768px;
  height: 600px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  z-index: 100;
  box-shadow: 0px 0px 50px 20px #8f8f8f;
}

.arrow_icon{
	font-size: 130px;
	color: #00ff95;
	position: absolute;
	top: 80%;
	left: 80%;
	z-index: 150;
	animation: shine 1s infinite;
	-webkit-animation: shine 1s infinite;
}

.arrow_icon_left{
  top: 80%;
	left: 10%;
}

@keyframes shine {
	0% {
		color: #00ff95;
	}
	100% {
		color:#efff61;
	}
}

.close_icon{
  font-size: 50px;
	color: #003b23;
	position: absolute;
	top: 0;
	right: 0;
	z-index: 150;
}

.text-box{
  margin-top: 100px;
}

.text-box2{
  margin-top: 100px;
  margin-left: 400px;
  text-align: left;
}